$(document).ready(function() {
    var hasMainLayout = $('#tags-panel').length > 0;
    if (hasMainLayout) {
        var mainLayout = {
            defaults: {
                applyDefaultStyles: true,
            },
            west: {
                size: 380,
            },
            north: {
                applyDefaultStyles: false,
                closable: false,
                pane_spacing: 0,
                resizable: false,
                slidable: false,
                spacing_open: 0,
            },
            center: {
                minWidth: 50,
            },
            east: {
                size: 400,
                onresize: 'tagger.updateCurrentTagsPosition()',
                onclose: 'tagger.updateCurrentTagsPosition()',
            },
        };
        if (tagger.westSize) {
            mainLayout.west = {
                size: tagger.westSize,
            };
        }

        $('#appBody').layout(mainLayout);

        tagger.switchObjectsPanelTab('tree');

        tagger.updateSelectedResources();

        $('#tags-panel').draggable({
            axis: 'y',
            handle: '.tagger-tags-panel-header',
            appendTo: '#objectsPanel',
        });
        tagger.updateCurrentTagsPosition();
    }
});

var tagger = {
    hash: window.location.hash,
    objectGraphURI: null,
    objectsPanelTab: 'tree', // or "search" or "recommend"
    objectsPanelLayout: null,
    selectedSubjectURI: null,
    selectedObjectURI: null,
    subjectSearchMode: false,
    subjectsPanelLayout: null,
};

tagger.autoTagSearchResults = function(searchFormId) {
    swa.loadSearchResultsList(searchFormId, 100000, function(resourceURIs, resourceType) {
        if (resourceURIs.length == 0) {
            swa.populateModalDialog('No matching instances found.', null, null);
            return;
        }
        var list = '';
        for (var i = 0; i < resourceURIs.length; i++) {
            if (list.length > 0) {
                list += ' ';
            }
            list += resourceURIs[i];
        }
        var params = {
            _viewClass: 'autotagger:AutoTagResourcesService',
            _base: swa.queryGraphURI,
            resources: list,
        };

        let params2 = new URLSearchParams(params);

        return axiosInstance
            .post('swp', params2)
            .then(() => {
                swa.populateModalDialog(
                    'AutoClassifier started on ' +
                        resourceURIs.length +
                        ' resources. Progress can be monitored from the AutoClassifier tab, and once completed the results will be available as a new Working Copy.',
                    null,
                    null
                );
            })
            .catch((err) => {
                swa.populateModalDialog('Operation failed: ' + err, 'Operation failed', null);
            });
    });
};

