var acui = {
    toggleACDocumentChangeView: function(bodyId, buttonId, graphURI, tagURI, documentURI) {
        var open = $('#' + buttonId).hasClass('ui-icon-triangle-1-s');
        swa.toggleOpenableObject(bodyId, buttonId);
        var body = $('#' + bodyId);
        var hasContent = body.children().length > 0;
        if (!hasContent) {
            var params = {
                _viewClass: 'autotagger:ResultDocumentChangeView',
                projectGraph: '<' + graphURI + '>',
                tag: '<' + tagURI + '>',
                subject: '<' + documentURI + '>',
                _snippet: true,
            };
            body.load(swa.servlet, params, function(data) {});
        }
    },
};

export default acui;
