import React from 'react';
import PropTypes from 'prop-types';

import DataProviders from 'DataProviders';
import { parse, print } from 'graphql';
import gql from 'graphql-tag';
import * as DTRenders from 'dtrenders';
import * as DTParsers from 'dtparsers';
import * as TableHelpers from 'TableHelpers';
import TBBreakout from 'TBBreakout';
import * as searchHelper from 'SearchHelper';

const getRenderMethod = (col, editable) => {
    if (!editable) {
        return null;
    }

    //coming from the server
    if (col.hasOwnProperty('render') && col.render === 'taggerColumn') {
        return DTRenders.taggerColumn;
    }

    return TableHelpers.getRenderMethod(col, editable);
};

class TaggerTableComponent extends React.Component {
    constructor(props) {
        super(props);

        let element = document.getElementById('tb-results-table');

        if (element) {
            this.tableOptions = searchHelper.getResultsTableData();
            this.tableElement = element;
        } else {
            this.tableOptions = {};
        }

        this.tableOptions.getRenderMethod = getRenderMethod;
        this.tableOptions.mutationHandler = this.props.mutationHandler;
        this.tableOptions.canSelectRows = false;
        this.tableOptions.hasButtons = false;
        this.tableOptions.hasSelectAll = false;
        this.tableOptions.hasViewDetails = false;
        this.tableOptions.hasMultiSelect = false;
        this.tableOptions.hideAllButtons = true;
    }

    executeQuery(table) {
        //ask graphql to get the data
        const template = table.buildQueryTemplate();

        if (template) {
            table.setState(
                {
                    loading: true,
                },
                () => {
                    if (process.env.NODE_ENV !== 'production') {
                        console.log(print(parse(template)));
                    }

                    table.props.GQLClient.query({ query: gql(template), fetchPolicy: 'network-only' })
                        .then((response) => {
                            let { matchTemplate, targetGraphURI } = table.props.columns[0];
                            let tagColumns = table.props.columns.filter((col) => col.render === 'taggerColumn');

                            DataProviders.SWP.getMatchesByColumn(targetGraphURI, matchTemplate, tagColumns)
                                .then((res) => {
                                    let data = DTParsers.parseDataForTagSets(response.data.results, res, tagColumns);

                                    table.setState({
                                        loading: false,
                                        template: template,
                                        data: data,
                                        searchRequest: table.store.getState().searchRequest,
                                    });
                                })
                                .catch((err) => {
                                    table.setState({
                                        loading: false,
                                        template: template,
                                        data: [],
                                        searchRequest: table.store.getState().searchRequest,
                                    });
                                    console.log(err);
                                });

                            table.setState({
                                loading: false,
                                template: template,
                                data: response.data.results,
                                searchRequest: table.store.getState().searchRequest,
                            });
                        })
                        .catch((err) => {
                            table.setState({
                                loading: false,
                                template: template,
                                data: [],
                                searchRequest: table.store.getState().searchRequest,
                            });
                            swa.populateModalDialog(
                                'Error - the currently selected viewpoint class does not have the proper shacl shapes set up to work with GraphQL'
                            );
                            console.log(err);
                        });
                }
            );
        }
    }

    render() {
        return (
            <SearchContext.Consumer>
                {(search) => (
                    <TBBreakout el={this.tableElement}>
                        <ResultsTableComponent
                            ref={this.props.resultsRef}
                            search={search}
                            columns={search.columns}
                            GQLClient={this.props.GQLClient}
                            resourceType={search.resourceType}
                            tableOptions={this.tableOptions}
                            store={this.props.store}
                            domReference={this.tableElement}
                            baseGraph={this.props.baseGraph}
                            appName={this.props.appName}
                            executeQuery={this.executeQuery}
                            isTagger={true}
                        />
                    </TBBreakout>
                )}
            </SearchContext.Consumer>
        );
    }
}

TaggerTableComponent.propTypes = {
    mutationHandler: PropTypes.func,
};
export default TaggerTableComponent;
