import React from 'react';
import ReactDOM from 'react-dom';

import * as searchHelper from 'SearchHelper';
import DataProviders from 'DataProviders';
import SearchFiltersComponent from 'SearchFiltersComponent';
import * as ComponentFactory from 'ComponentFactory';

import ColumnsTagsComponent from './tag-columns-list';
import TaggerTableComponent from './tagger-table-results';
import SearchState from 'SearchState';

import store from 'store';

class TaggerAppComponent extends React.Component {
    constructor(props) {
        super(props);
        this.store = props.store;
        this.resultsRef = React.createRef();

        this.handleResourceChange = this.handleResourceChange.bind(this);
        this.handleSearchChange = this.handleSearchChange.bind(this);
        this.handleSearchRequest = this.handleSearchRequest.bind(this);
        this.mutationHandler = this.mutationHandler.bind(this);

        let search = new SearchState({
            resourceType: props.resourceType || props.selectedResourceType,
            filters: props.filters || props.searchFilters,
            columns: props.columns || props.searchColumns,
            tagColumns: props.tagColumns,
            isLocal: props.isLocal,
            maxRows: props.maxRows,
            updateDelegate: this.handleSearchChange,
            searchDelegate: this.handleSearchRequest,
        });

        this.state = {
            search: search,
            tagColumns: props.tagColumns,
            hasAssetSelector: props.hasAssetSelector,
            hasHierarchyViewButton: props.hasHierarchyViewButton,
            currentUserIsManager: props.currentUserIsManager,
        };
    }

    handleSearchChange(search) {
        if (search) {
            this.setState({
                search: search,
            });
        }
    }

    handleSearchRequest() {
        if (this.resultsRef) {
            this.resultsRef.current.requestSearch();
        }
    }

    handleResourceChange(resourceURI) {
        let appData = searchHelper.getFiltersAppDataFromDOM();

        const GQLClient = DataProviders.GraphQL.getGQLClient(appData.graphId, appData.tagId);

        let getDefaults = null;

        if (!resourceURI) {
            getDefaults = Promise.resolve();
        } else {
            getDefaults = DataProviders.UNIFIED.getDefaultTaggerSearch(resourceURI, GQLClient);
        }

        getDefaults
            .then((appState) => {
                this.state.search.setState(appState);
            })
            .catch((err) => {
                console.log('err', err);

                swa.populateModalDialog(`Changing viewpoint failed: ${err}`, 'Operation failed', null);
            });
        return;
    }

    componentWillUnmount() {
        if (this.unsubscribe) {
            this.unsubscribe();
        }
    }

    mutationHandler(DataProvider, columns, method, url, data, success, error) {
        let uri = Object.keys(data.data)[0];
        let otherChanges = data.data[uri];
        let targetColData = Object.keys(data.data[uri])[0];

        DataProvider.getAsset(uri).then((response) => {
            let results = JSON.parse(JSON.stringify(response.data.results));

            if (otherChanges[targetColData]) {
                results[0][targetColData] = otherChanges[targetColData];

                let data = JSON.parse(JSON.stringify(this.resultsRef.current.state.data));
                let dataKeys = data.map((col) => col.uri);
                let currentColIndex = dataKeys.indexOf(Object.keys(otherChanges[targetColData])[0]);

                data[currentColIndex][targetColData] = results[0][targetColData];

                this.resultsRef.current.setState({ data: data });
            }

            //nothing changed, [pk is false positive] return success
            return success({});
        });
    }

    render() {
        return (
            <SearchContext.Provider value={this.state.search}>
                <SearchFiltersComponent
                    store={this.store}
                    search={this.state.search}
                    GQLClient={this.props.GQLClient}
                    appName={this.props.appName}
                    baseGraph={this.props.baseGraph}
                    showMappingsButton={this.props.showMappingsButton}
                    hideFiltersList={this.props.hideFiltersList}
                    hidePropertiesHierarchyView={true}
                    onResourceSelected={this.handleResourceChange}
                    hideLocalSearch={true}
                    isLocal={this.state.search.isLocal}
                    hasAssetSelector={true}
                    hasHierarchyViewButton={this.props.hasHierarchyViewButton}
                    currentUserIsManager={this.state.currentUserIsManager}
                    actionsBar={() => {
                        return (
                            <React.Fragment>
                                {
                                    <ColumnsTagsComponent
                                        props={this.props}
                                        GQLClient={this.props.GQLClient}
                                        search={this.state.search}
                                    />
                                }
                                {ComponentFactory.getFiltersListComponent(
                                    this.props.GQLClient,
                                    this.props.store,
                                    this.props.baseGraph,
                                    false,
                                    'Filter By Source'
                                )}
                                {ComponentFactory.getActionMenuButtonComponent(
                                    true,
                                    this.props.GQLClient,
                                    this.props.appName,
                                    this.props.baseGraph,
                                    this.props.store,
                                    this.props.maxRows,
                                    this.props.currentUserIsManager
                                )}
                            </React.Fragment>
                        );
                    }}
                />
                <TaggerTableComponent
                    GQLClient={this.props.GQLClient}
                    resultsRef={this.resultsRef}
                    columns={[]}
                    store={this.props.store}
                    baseGraph={this.props.baseGraph}
                    appName={this.props.appName}
                    mutationHandler={this.mutationHandler}
                />
            </SearchContext.Provider>
        );
    }
}

export default TaggerAppComponent;

const initResultsApp = (appData) => {
    let hasComponents = {
        hasAssetSelector: false,
        hasHierarchyViewButton: false,
    };

    let appContainer = document.getElementById('filters-app');
    ReactDOM.render(
        <TaggerAppComponent
            {...hasComponents}
            GQLClient={appData.GQLClient}
            resourceType={appData.resourceType}
            columns={appData.columns}
            tagColumns={appData.tagColumns}
            filters={appData.filters}
            baseGraph={appData.baseGraph}
            appName={appData.appName}
            store={appData.store}
            graphId={appData.graphId}
            tagId={appData.tagId}
            maxRows={appData.maxRows}
            currentUserIsManager={appData.currentUserIsManager}
            hideFiltersList={appData.hideFiltersList}
            hideSavedSearchActions={appData.hideSavedSearchActions}
            disableLocalSearch={appData.disableLocalSearch}
            isLocal={appData.isLocal || appData.localSearchState}
            projectGraph={appData.projectGraph}
        />,
        appContainer
    );
};

const initView = () => {
    let doesPageHaveElement = document.getElementById('filters-app') ? true : false;

    if (doesPageHaveElement) {
        let appData = searchHelper.getFiltersAppDataFromDOM(),
            hideFiltersList = false;

        appData.isLocal = false;

        const GQLClient = DataProviders.GraphQL.getGQLClient(appData.graphId, appData.tagId);

        let getDefaults = null;

        if (!appData.resourceType) {
            getDefaults = Promise.resolve();
        } else {
            getDefaults = DataProviders.UNIFIED.getDefaultTaggerSearch(appData.resourceType, GQLClient);
        }

        getDefaults
            .then((appState) => {
                let getAvailableTagColumns = null;
                let resourceType = null;

                getAvailableTagColumns = DataProviders.SWP.getAvailableTagColumns(
                    appState.searchColumns[0].targetGraphURI
                );

                getAvailableTagColumns.then((tagColumns) => {
                    if (appState) {
                        store.dispatch(swa.actions.applySearchState(appState));
                        resourceType = appState.selectedResourceType;
                        appData.columns = appState.searchColumns;
                        appData.tagColumns = tagColumns;
                        appData.filters = appState.searchFilters;
                    }
                    initResultsApp({
                        GQLClient,
                        store,
                        ...appData,
                        resourceType,
                        hideFiltersList,
                    });
                });
            })
            .catch((err) => {
                if (err.includes('SHACL shapes')) {
                    swa.populateModalDialog(err).then(() => {
                        let ref = window.location.href.split('?');
                        window.location.href = ref[0] + '?_editorClass=taggerui%3AApplication';
                    });
                } else {
                    swa.populateModalDialog(`Restoring Default Search failed:  ${err}`, 'Operation failed', null);
                }
            });
        return;
    }
};

const initApp = () => {
    $(document).ready(function() {
        const element = document.querySelector('[data-page-type*=tagger-editor-page]');

        if (element) {
            initView();
        }
    });
};

initApp();
