import React from 'react';

import * as searchHelper from 'SearchHelper';
import SearchContext from 'SearchContext';

import { withToggleContainer } from 'ToggleContainer';

class ColumnsTagsComponent extends React.Component {
    constructor(props) {
        super(props);

        let element = document.getElementById('tb-results-table');

        if (element) {
            this.tableOptions = searchHelper.getResultsTableData();
            this.tableElement = element;
        } else {
            this.tableOptions = {};
        }

        this.state = {
            tagColumns: props.props.tagColumns,
            search: props.search,
        };

        this.getColumnData = this.getColumnData.bind(this);
        this.selectColumn = this.selectColumn.bind(this);

        this.tableOptions.mutationHandler = this.props.mutationHandler;
        this.tableOptions.canSelectRows = false;
        this.tableOptions.hasButtons = false;
        this.tableOptions.hasSelectAll = false;
        this.tableOptions.hasViewDetails = false;
        this.tableOptions.hasMultiSelect = false;
        this.tableOptions.hideAllButtons = true;
    }

    getColumnData() {
        let tagVals = this.state.search.columns.map((col) => col.name);
        return this.state.tagColumns.filter(
            (col) =>
                col.name !== 'uri' &&
                col.name !== 'label' &&
                this.state.search.columns.map((col) => col.name).indexOf(col.name) === -1
        );
    }

    selectColumn(column) {
        let colPath = `${this.state.path ? this.state.path + '.' : ''}${column.name}`;
        let colPathObj = { path: colPath, pathLabel: this.state.pathLabel };

        if (this.state.search.columns.indexOf(column) === -1) {
            //if -1, column is currently not selected
            this.state.search.addColumn(column);
        } else {
            //column is currently selected, remove it
            this.state.search.removeColumn({ ...column, ...colPathObj });
        }
    }

    render() {
        return (
            <SearchContext.Consumer>
                {(search) => (
                    <div className="list">
                        <button
                            disabled={search.resourceType == null ? true : false}
                            type="button"
                            className={`btn btn-default ${this.props.className}`}
                            onClick={this.props.toggleContainer}
                            aria-haspopup="true"
                            aria-expanded="false"
                        >
                            {this.props.label || 'Tag Columns'}
                            <span className="caret" />
                        </button>
                        <TBRelativePortal forwardRef={this.props.toggleRef} leftOffset={-325}>
                            <ColumnsList
                                GQLClient={this.props.GQLClient}
                                getColumnData={this.getColumnData}
                                resourceType={search.resourceType}
                                selectedColumns={search.columns}
                                baseGraph={this.props.baseGraph}
                                store={this.props.store}
                                isOpen={this.props.isOpen}
                                onSelectFilter={this.selectColumn}
                            />
                        </TBRelativePortal>
                    </div>
                )}
            </SearchContext.Consumer>
        );
    }
}

export default withToggleContainer(ColumnsTagsComponent);
